import React from 'react';
import styled from 'styled-components';

const AvatarStyled = styled.img`
  ${(props: { size: number }) => ({
    width: `${props.size}px`,
    height: `${props.size}px`,
  })};
  margin-right: 15px;
  border-radius: 50%;
  object-fit: cover;
`

type AvatarProps = {
  src: string
  alt: string
  size?: number
}

export const Avatar: React.FC<AvatarProps> = ({src, alt, size= 25 }) =>
  <AvatarStyled src={src} alt={alt} size={size} />